import { test, txt } from './const';
import Butterfly from './butterfly';

const MAX_BUTTERFLY = 50;
const SPLIT_COUNT = 12;

class TypingPrinter {
    constructor(txt){
        this.txt = txt;
        return this.init();
    }
    // generate split array for display
    init() {
        const txtArr = this.txt.split('');

        this.splitArr = [];
        let tmp = [];
        let count = 0;
        txtArr.forEach(word => {
            tmp.push(word);
            if (word == '\n') {
                count++;
            }
            if(count == SPLIT_COUNT){
                this.splitArr.push(tmp);
                count = 0;
                tmp = [];
            }
        });

        return this.genTimeFn();
    }
    genTimeFn(){
        const res = [];
        for (let i = 0, len = this.splitArr.length; i < len; i++) {
            res.push({ fn: this.genHTML.bind(this, this.splitArr[i]), time: i > 0 ? this.splitArr[i].length * 100 : 0 });
        }
        return res;
    }
    genHTML(wordArr){
        const html = wordArr.map((word, i) => {
            const delayStr = `-webkit-animation-delay: ${i * 100}ms; animation-delay: ${i * 100}ms;`
            switch(word) {
                case '\n':
                    return '<br />';
                case ' ':
                    return `<span class="txt">&nbsp;<i class="mask" style="${delayStr}"></i></span>`;
                default:
                    return `<span class="txt">${word}<i class="mask" style="${delayStr}"></i></span>`;
            }
        }).join('');

        const div = document.createElement("div");
        div.className = "txt-wrapper";
        div.innerHTML = html;        
        
        document.body.appendChild(div);
    }
}

let index = 0;
const fns = new TypingPrinter(txt);
let start = Date.now();
let bCount = 0;
console.log(fns);
setInterval(() => {
    const now = Date.now();
    
    if (fns[index] && fns[index].time < now - start) {
        fns[index].fn();
        start = Date.now();
        index++;
    }

    if (bCount < MAX_BUTTERFLY) {
        bCount++;
        new Butterfly
    }
}, 500);

